//logo color variable
$logo-main-color: #d74199;
$logo-fill-color: rgba($logo-main-color, 1);
$logo-0:rgba($logo-main-color, 0);
$logo-03:rgba($logo-main-color, 0.3);
$logo-06:rgba($logo-main-color, 0.6);

.logo_container {
    margin-top: 1rem;

    @media (max-width: 768px) {
        display: flex;
        margin-top: 0;
    }
}

#logo {
    font-family: 'Zen Dots', cursive;
    fill: $logo-fill-color;

    text {
        text-transform: uppercase;
        font-size: 3.3rem;
        stroke-width: 2;
        stroke: $logo-fill-color;
        stroke-dasharray: 500;
        stroke-dashoffset: 0;

        animation: stroke 5s linear;

        @media (max-width: 768px) {
            font-size: 2.3rem;
        }

    }

    @keyframes stroke {

        0% {
            stroke-dashoffset: 0;
            fill: $logo-0;
        }

        40% {
            stroke-dashoffset: 500;
            fill: $logo-03;
        }

        80% {
            stroke-dashoffset: 1000;
            fill: logo-06;
        }

        100% {
            stroke-dashoffset: 1000;
            fill: $logo-fill-color;

        }
    }

    &:hover {
        cursor: pointer;
        fill: none;
    }

    @media (max-width: 768px) {}

}

#logo_figures {
    margin-right: 1rem;

    &:hover {
        cursor: pointer;
        fill: none;
    }

    @media (max-width: 768px) {
        width: 80px;
        margin-right: 0rem;
    }

}